.shopTitle {
   margin-top: 50px;
   text-align: center;
   font-size: 20px;
 }
 .products {
   width: 100%;
   height: auto;
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   place-items: center;
 }

.product {
      border-radius: 20px;
      width: 100px;
      height: auto;
      margin-top: 10px;
      margin-bottom: 10px;
      margin-right: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    
    .product img {
      width: 250px;
      height: 280px;
    }
    
    .product .description {
      text-align: center;
    }
    
    .product:hover {
      transition: 0.3s ease-in;
      cursor: pointer;
    }
    
    .addToCartBttn {
      background-color: transparent;
      border: 2px solid rgb(19, 19, 19);
      margin-top: 10px;
      margin-right:10px;
      min-width: 100px;
      padding-left: 10px;
      padding-right: 10px;
      padding-top: 2px;
      padding-bottom: 5px;
      border-radius: 15px;
    }
    
    .addToCartBttn:hover {
      background-color: rgb(19, 19, 19);
      color: white;
      cursor: pointer;
    }

    /* 1fr: This is a unit of measurement called a "fraction." It represents a fraction of the available space in the grid container. In this case, it means that the first column should take up 1/3 of the available space (because there are three columns in total).So, when you set grid-template-columns: 1fr 1fr 1fr;, you're creating a grid with three equally sized columns.  */